___CSS_LOADER_EXPORT___.push([module.id, `:root {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  --gameboard-col: #e1e1e1;
  --header-footer-col: #1d1d1d;
  --mild-white: rgb(250, 250, 250);
  --correct-green: #37eb31;
  --incorrect-red: #fe0000;
  --img-width: 150px;
}

* {
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--mild-white);
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

header {
  font-size: 1rem;
  font-weight: bold;
}

.content-container {
  max-width: 1000px;
  width: 90%;
  margin: 0 auto;
}

header > .content-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
}

main {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

footer {
  font-weight: bold;
  padding: 0.2rem 0;
}

.game-board {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 50vh;
}

.game-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  background-color: var(--mild-white);
  padding: 1.5rem;
}

.game-card .img-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: var(--img-width);
}

.game-card,
button {
  font-size: 1.5rem;
  font-weight: bold;
}

.button-container {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  gap: 3rem;
}

button {
  padding: 0.5em 2em;
}

.game-card .img-container img {
  width: var(--img-width);
}

.game-card .dir-container p {
  text-align: center;
}

button,
.game-card,
.game-board {
  border: 2px solid var(--header-footer-col);
  border-radius: 10px;
  box-shadow: var(--header-footer-col) 4px 4px 0 0;
  color: var(--header-footer-col);
}

button,
.game-board {
  background-color: var(--gameboard-col);
}

header,
footer {
  background-color: var(--header-footer-col);
  color: var(--gameboard-col);
}

.correct {
  background-color: var(--correct-green);
}

.incorrect {
  background-color: var(--incorrect-red);
}

button {
  cursor: pointer;
}

button:hover {
  background-color: var(--mild-white);
}

button:active {
  box-shadow: var(--header-footer-col) 2px 2px 0 0;
  transform: translate(2px, 2px);
}

.disabled,
button:hover.disabled {
  background-color: darkgrey;
}

.modal {
  min-height: 50vh;
  z-index: 1;
  background-color: white;
  padding: 2rem;
  position: fixed;
  border: solid 2px black;
  border-radius: 10px;
  line-height: 1.25;
  overflow: auto;
}

.how-to-play {
  visibility: hidden;
}

.start-game-modal {
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal h2 {
  margin-bottom: 1.5rem;
}

.modal p {
  margin-bottom: 0.6rem;
}

.modal-img {
  height: 100%;
  max-height: 250px;
  border: solid 2px black;
  border-radius: 5px;
}

.modal-content-container {
  display: flex;
  gap: 2rem;
}

.close-modal-btn {
  position: absolute;
  font-size: 0.8rem;
  top: 15px;
  right: 15px;
  height: 1rem;
  width: 1rem;
  padding: 0.5rem;
  padding-bottom: 0.6rem;
  border: solid 2px black;
  border-radius: 50%;

  display: flex;
  justify-content: center;
  align-items: center;
}

#how-to-btn {
  font-size: 1rem;
  border-style: none;
}
